import { DxUIElement } from "./dx-ui-element";
import { Locker } from "../locker";
export default class DxSupportCaptureElement extends DxUIElement {
    constructor() {
        super(...arguments);
        this.branchId = "";
        this.parentBranchId = null;
        this._isCaptured = false;
        this._locker = null;
        this.capturingAction = () => { };
        this.releasingCaptureAction = () => { };
    }
    get locker() {
        return this._locker || (this._locker = new Locker());
    }
    get isCaptured() {
        return this._isCaptured;
    }
    gotCapture() {
        if (!this._isCaptured) {
            this._isCaptured = true;
            this.capturingAction();
        }
    }
    lostCapture() {
        if (this._isCaptured) {
            this._isCaptured = false;
            this.releasingCaptureAction();
        }
    }
    get isCloseOnPointerUp() {
        return false;
    }
}
