class SimpleEvent {
    constructor() {
        this.handlers = [];
        this.raise = ((args) => {
            this.handlers.slice(0).forEach(h => h(args));
        });
    }
    subscribe(handler) {
        this.handlers.push(handler);
    }
    unsubscribe(handler) {
        this.handlers = this.handlers.filter(h => h !== handler);
    }
}
class Signal {
    constructor() {
        this.handlers = [];
    }
    subscribe(handler) {
        this.handlers.push(handler);
    }
    unsubscribe(handler) {
        this.handlers = this.handlers.filter(h => h !== handler);
    }
    raise(source, data) {
        this.handlers.slice(0).forEach(h => h(source, data));
    }
}
class AsyncSignal {
    constructor() {
        this.handlers = [];
    }
    subscribe(handler) {
        this.handlers.push(handler);
    }
    unsubscribe(handler) {
        this.handlers = this.handlers.filter(h => h !== handler);
    }
    async raise(source, data) {
        this.handlers.slice(0).map(h => h(source, data));
    }
    async await(source, data) {
        const promises = this.handlers.slice(0).map(h => h(source, data));
        await Promise.all(promises);
    }
    expose() {
        return this;
    }
}
export { SimpleEvent, Signal, AsyncSignal };
