export class TaskCompletionSource {
    constructor() {
        this._isResolved = false;
        this._isRejected = false;
        this._promise = new Promise((resolve, reject) => {
            this._resolve = resolve;
            this._reject = reject;
        });
    }
    get promise() {
        return this._promise;
    }
    resolve(value) {
        this._isResolved = true;
        this._resolve(value);
    }
    reject(reason) {
        this._isRejected = true;
        this._reject(reason);
    }
    tryResolve(value) {
        if (!this._isResolved && !this._isRejected)
            this.resolve(value);
    }
    tryReject(reason) {
        if (!this._isResolved && !this._isRejected)
            this.reject(reason);
    }
}
