import { Size } from "./size";
const resizeObserver = new ResizeObserver((entries) => {
    for (const entry of entries) {
        const container = (entry.target);
        container.actualSize = new Size(Math.round(entry.contentRect.width), Math.round(entry.contentRect.height));
    }
});
export function attachToSizeChangeNotifications(element) {
    resizeObserver.observe(element);
}
export function detachFromSizeChangeNotifications(element) {
    resizeObserver.unobserve(element);
}
